<template>
  <div>
    <!-- 评论列表 -->
    <div class="cmt-list" :class="{ 'art-cmt-container-1': !isWrite, 'art-cmt-container-2': isWrite }">

      <!-- 加载更多评论组件 -->
      <van-list v-model="loading" :finished="finished" finished-text="没有更多评论了" @load="onLoad" :immediate-check="false"
        offset="50">
        <!-- 评论的 Item 项 -->
        <div class="cmt-item" v-for="item in commentList" :key="item.com_id">
          <!-- 头部区域 -->
          <div class="cmt-header">
            <!-- 头部左侧 -->
            <div class="cmt-header-left">
              <img :src="item.aut_photo" alt="" class="avatar">
              <span class="uname">{{ item.aut_name }}</span>
            </div>
            <!-- 头部右侧 -->
            <div class="cmt-header-right">
              <van-icon name="like" size="16" color="red" v-if="item.is_liking === true"
                @click.native="likeFn(true, item)" />
              <van-icon name="like-o" size="16" color="gray" v-else @click.native="likeFn(false, item)" />
            </div>
          </div>
          <!-- 主体区域 -->
          <div class="cmt-body">
            {{ item.content }}
          </div>
          <!-- 尾部区域 -->
          <div class="cmt-footer">{{ timeAgo(item.pubdate) }}</div>

        </div>
      </van-list>
    </div>

    <!-- 底部添加评论区域 - 1 -->
    <div class="add-cmt-box van-hairline--top" v-if="isWrite === false">
      <van-icon name="arrow-left" size="0.48rem" @click="$router.back()" />
      <div class="ipt-cmt-div" @click="pubFn">发表评论</div>
      <div class="icon-box">
        <van-badge :content="totalCount === 0 ? '' : totalCount" max="99">
          <van-icon name="comment-o" size="0.53333334rem" @click="commentClickFn" />
        </van-badge>
        <van-icon name="star-o" size="0.53333334rem" />
        <van-icon name="share-o" size="0.53333334rem" />
      </div>
    </div>

    <!-- 底部添加评论区域 - 2 -->
    <div class="cmt-box van-hairline--top" v-else>
      <textarea placeholder="友善评论、理性发言、阳光心灵" v-selfFocus @blur="blurFn" v-model="comText"></textarea>
      <van-button type="default" :disabled="comText.length > 0 ? false : true" @click="sendFn">发布</van-button>
    </div>
  </div>
</template>

<script>
// 导入评论列表API
import { commentListAPI, commentLikingAPI, commentDisLikingAPI, sendCommentAPI } from '@/api/index'
import { timeAgo } from '@/utils/date'

export default {
  name: 'CommentList',
  data() {
    return {
      offset: null,
      commentList: [],
      isWrite: false,
      totalCount: 0,
      comText: '', // 发布评论的内容
      loading: false,
      finished: false
    }
  },
  methods: {
    timeAgo: timeAgo,
    // 评论点赞
    async likeFn(bool, obj) {
      if (bool) {
        // 取消点赞
        obj.is_liking = false
        await commentDisLikingAPI({ comId: obj.com_id })
      } else {
        // 点赞
        obj.is_liking = true
        await commentLikingAPI({ comId: obj.com_id })
      }
    },
    // 点击展示输入评论内容的文本框
    pubFn() {
      this.isWrite = true
    },
    // 评论条数点击按钮，点击滑到评论区
    commentClickFn() {
      // const artHeight = document.querySelector('.article-container').scrollHeight
      // window.scrollTo(0, artHeight)

      // 该滚动方法不会那么生硬，.like-box代表滚动到哪，这是点赞那里，下面就是评论区了
      document.querySelector('.like-box').scrollIntoView({
        behavior: 'smooth'
      })
    },
    // 评论框失去焦点
    blurFn() {
      // 设置定时器宏任务后执行，否则点击发布弹框失去焦点移除标签，就得不到评论的内容了。让发布点击事件先执行
      setTimeout(() => {
        this.isWrite = false
      }, 0)
    },
    // 发布评论
    async sendFn() {
      const { data: res } = await sendCommentAPI({ artId: this.$route.query.art_id, content: this.comText })
      this.commentList.unshift(res.data.new_obj)
      this.totalCount++
      this.comText = ''
      document.querySelector('.like-box').scrollIntoView({
        behavior: 'smooth'
      })
    },
    // 加载更多评论
    async onLoad() {
      if (this.commentList.length > 0) {
        const { data: res } = await commentListAPI({ artId: this.$route.query.art_id, offset: this.offset })
        this.commentList = [...this.commentList, ...res.data.results]
        this.totalCount = res.data.total_count
        this.offset = res.data.last_id
        if (res.data.last_id === null) {
          this.finished = true
        }
        this.loading = false
      } else {
        this.loading = false
      }
    }
  },
  async created() {
    const { data: res } = await commentListAPI({ artId: this.$route.query.art_id })
    if (res.data.results.length === 0) {
      this.finished = true
    }
    this.commentList = res.data.results
    this.totalCount = res.data.total_count
    this.offset = res.data.last_id
  }
}
</script>

   <style scoped lang="less">
.cmt-list {
  padding: 10px;

  .cmt-item {
    padding: 15px 0;

    +.cmt-item {
      border-top: 1px solid #f8f8f8;
    }

    .cmt-header {
      display: flex;
      align-items: center;
      justify-content: space-between;

      .cmt-header-left {
        display: flex;
        align-items: center;

        .avatar {
          width: 40px;
          height: 40px;
          background-color: #f8f8f8;
          border-radius: 50%;
          margin-right: 15px;
        }

        .uname {
          font-size: 12px;
        }
      }
    }

    .cmt-body {
      font-size: 14px;
      line-height: 28px;
      text-indent: 2em;
      margin-top: 6px;
      word-break: break-all;
    }

    .cmt-footer {
      font-size: 12px;
      color: gray;
      margin-top: 10px;
    }
  }
}

.art-cmt-container-1 {
  padding-bottom: 46px;
}

.art-cmt-container-2 {
  padding-bottom: 80px;
}

// 发布评论的盒子 - 1
.add-cmt-box {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  box-sizing: border-box;
  background-color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 46px;
  line-height: 46px;
  padding-left: 10px;

  .ipt-cmt-div {
    flex: 1;
    border: 1px solid #efefef;
    border-radius: 15px;
    height: 30px;
    font-size: 12px;
    line-height: 30px;
    padding-left: 15px;
    margin-left: 10px;
    background-color: #f8f8f8;
  }

  .icon-box {
    width: 40%;
    display: flex;
    justify-content: space-evenly;
    line-height: 0;
  }
}

.child {
  width: 20px;
  height: 20px;
  background: #f2f3f5;
}

// 发布评论的盒子 - 2
.cmt-box {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 12px;
  padding-left: 10px;
  box-sizing: border-box;
  background-color: white;

  textarea {
    flex: 1;
    height: 66%;
    border: 1px solid #efefef;
    background-color: #f8f8f8;
    resize: none;
    border-radius: 6px;
    padding: 5px;
  }

  .van-button {
    height: 100%;
    border: none;
  }
}
</style>
