<template>
  <div>
    <!-- Header 区域 -->
    <van-nav-bar fixed title="文章详情" left-arrow @click-left="$router.back()" />

    <!-- 文章信息区域 -->
    <div class="article-container">
      <!-- 文章标题 -->
      <h1 class="art-title">{{ artObj.title }}</h1>

      <!-- 用户信息 -->
      <van-cell center :title="artObj.aut_name" :label="timeAgo(artObj.pubdate)">
        <template #icon>
          <img :src="artObj.aut_photo" alt="" class="avatar">
        </template>
        <template #default>
          <div>
            <van-button type="info" size="mini" v-if="artObj.is_followed" @click="followedFn(true)">已关注</van-button>
            <van-button icon="plus" type="info" size="mini" plain v-else @click="followedFn(false)">关注</van-button>
          </div>
        </template>
      </van-cell>

      <!-- 分割线 -->
      <van-divider></van-divider>

      <!-- 文章内容 -->
      <div class="art-content" v-html="artObj.content"></div>

      <!-- 分割线 -->
      <van-divider>End</van-divider>

      <!-- 点赞 -->
      <div class="like-box">
        <van-button icon="good-job" type="danger" size="small" v-if="artObj.attitude === 1" @click="attitudeFn(true)">
          已点赞
        </van-button>
        <van-button icon="good-job-o" type="danger" plain size="small" v-else @click="attitudeFn(false)">点赞</van-button>
      </div>
    </div>

    <!-- 文章评论列表 -->
    <CommentList></CommentList>
  </div>
</template>

<script>
// 导入文章详情接口,关注和取关用户
import { articleDetailAPI, userFollowedAPI, userUnFollowedAPI, articleLikeAPI, articleDisLikeAPI } from '@/api/index'

// 导入时间处理的模块方法
import { timeAgo } from '@/utils/date'

// 文章评论列表
import CommentList from './CommentList.vue'

export default {
  name: 'ArticleDetail',
  components: { CommentList },
  data() {
    return {
      artObj: {}
    }
  },
  methods: {
    // 时间处理方法
    timeAgo: timeAgo,
    // 关注与否方法
    async followedFn(bool) {
      if (bool) {
        // 点击已关注-取关
        this.artObj.is_followed = false
        // 调用取关接口
        await userUnFollowedAPI({
          userId: this.artObj.aut_id
        })
      } else {
        // 点击关注-关注
        this.artObj.is_followed = true
        // 调用关注接口
        await userFollowedAPI({ userId: this.artObj.aut_id })
      }
    },
    // 赞
    async attitudeFn(bool) {
      if (bool) {
        // 取消点赞
        this.artObj.attitude = 0
        await articleDisLikeAPI({ artId: this.artObj.art_id })
      } else {
        // 点赞
        this.artObj.attitude = 1
        await articleLikeAPI({ artId: this.artObj.art_id })
      }
    }
  },
  async created() {
    // 发起请求
    const { data: res } = await articleDetailAPI({ art_id: this.$route.query.art_id })
    this.artObj = res.data
  }
}
</script>

   <style scoped lang="less">
.article-container {
  padding: 10px;
  margin-top: 46px;
}

.art-title {
  font-size: 16px;
  font-weight: bold;
  margin: 10px 0;
}

.art-content {
  font-size: 12px;
  line-height: 24px;
  width: 100%;
  overflow-x: scroll;
  word-break: break-all;

  /deep/ img {
    width: 100%;
  }

  /deep/ pre {
    white-space: pre-wrap;
    word-wrap: break-word;
  }
}

.van-cell {
  padding: 5px 0;

  &::after {
    display: none;
  }
}

.avatar {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: #f8f8f8;
  margin-right: 5px;
  border: none;
}

.like-box {
  display: flex;
  justify-content: center;
}
</style>
